<template>
  <div>
    <a-card>
      <a-form
        :form="form"
        :label-col="{ span: 5 }"
        :wrapper-col="{ span: 12 }"
        @submit.prevent="handleSubmit"
      >
        <a-form-item label="Allowance Name">
          <a-input
            v-decorator="[
              'calendar_name',
              { rules: [{ required: true, message: 'Field is required!' }] },
            ]"
          />
        </a-form-item>

        <a-form-item label="Department">
          <a-select default-value="monthly" @change="handleChange">
            <a-select-option value="monthly"> All </a-select-option>
            <a-select-option value="weekly"> Sales</a-select-option>
            <a-select-option value="hourly"> Engineering </a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item label="Allowance Type">
          <a-radio-group name="radioGroup" :default-value="1">
            <a-radio :value="1"> Constant </a-radio>
            <a-radio :value="2"> Segmented </a-radio>
          </a-radio-group>
        </a-form-item>
        <a-form-item label="Amount">
          <a-input
            v-decorator="[
              'amount',
              { rules: [{ required: true, message: 'Field is required!' }] },
            ]"
          />
        </a-form-item>

        <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
          <a-button type="primary" html-type="submit" :loading="loading">
            Submit
          </a-button>
        </a-form-item>
      </a-form>
    </a-card>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this, { name: "coordinated" }),
      image: null,
    };
  },
  methods: {
    onChange() {},
    handleChange() {},
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          this.$store.dispatch("addClients", values).then(() => {
            if (!this.error) {
              this.form.resetFields();
            }
          });
        }
      });
    },
  },
  computed: {
    ...mapState["clients"],
    loading() {
      return this.$store.state.loading;
    },
    error() {
      return this.$store.state.error;
    },
  },
  mounted() {
    this.$store.dispatch("getClients");
  },
};
</script>

<style></style>
